	module.exports = {"data":{"post":{"id":"/Users/lugang/My Github/LG-Blog-Gatsby/content/posts/2018-06-25--hello-world/index.md absPath of file >>> MarkdownRemark","html":"<p>伴随着学生机的到期，又迎来了一次新的”Hello World”，博客从Wordpress=>自己整=>Hexo=>Gatsby，在这曲折的更换过程中我越来越认识到<strong>没钱</strong>就老老实实弄静态页的真理。</p>\n<p>首先先来安利一波<a href=\"https://github.com/gatsbyjs/gatsby\">Gatsby</a>，对比当下较流行静态网站生成器Jekyll和Hexo，其核心优势在于其基于全新的Web技术，前端React + Webpack，后端(伪)数据读取使用的Graphql，还自带Google的PRPL标准（PWA的最佳实践），技术栈新得让人害怕。。。同样功能Vue阵营的<a href=\"https://github.com/nuxt/nuxt.js\">Nuxt</a>以及<a href=\"https://github.com/vuejs/vuepress\">VuePress</a>也可以做到。虽然当前两者因为比较新，相比较于Hexo以及Jekyll在Themes（Starter）的积累处于完败的阵势（又少又丑），但毕竟是面向未来的，相信以后应该还是会慢慢将Hexo和Jekyll给淘汰掉。</p>\n<p>初衷是想为Gatsby贡献一套Anime主题的Starter的，奈何青春所剩无几，React+Graphql+Dota2 = GG。过几天又得回去工作了。。。想必是怼不动了，还是交给年轻人或者效率高得令人发指的大佬们吧。</p>","fields":{"slug":"/hello-world/","prefix":"2018-06-25"},"frontmatter":{"title":"Hello World!!","author":"todaylg","category":"废话集","cover":{"childImageSharp":{"resize":{"src":"/LG-Blog-Gatsby/static/test0-2d4d7588bb566caf8cf87fed4cb30f71-160fa.png"}}}}},"authornote":{"id":"/Users/lugang/My Github/LG-Blog-Gatsby/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>todaylg</strong> </p>"},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/hello-world/","next":{"id":"/Users/lugang/My Github/LG-Blog-Gatsby/content/posts/2018-06-26--complete-design-1/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/complete-design-1/","prefix":"2018-06-26"},"frontmatter":{"title":"毕设小结（一）WebVR","category":"小结"}}}}

